import React from 'react';
import bezier from 'bezier';
import createReactClass from 'create-react-class';
import Draggable from 'react-draggable';

const cubic = bezier.prepare(4);
const width = 1200, height = 400;
const styles = {
    parent: {
        width: `${width}px`,
        height: `${height}px`,
    },
    handle: {
        position: 'absolute',
        width: '10px',
        height: '10px',
        borderRadius:'100%',
    },
    canvas: {
        position: 'absolute',
        zIndex: 0,
    },
    controlPoints: {
        position: 'absolute',
        zIndex: 10,
    }
};


function generateBezier(controls) {
    const x = [], y = [];
    for (let i = 0; i < controls.length; i++) {
        if (controls[i].a) {
            x.push(controls[i].a.x);
            y.push(controls[i].a.y);
        }
        x.push(controls[i].b.x);
        y.push(controls[i].b.y);
        if (controls[i].c) {
            x.push(controls[i].c.x);
            y.push(controls[i].c.y);
        }

    }
    const bezierPoints = [];
    for (let i = 0; i < x.length; i += 3) {
        let cx = x.slice(i, i + 4), cy = y.slice(i, i + 4);
        for (let t = 0; t <= 1; t += 0.01) {
            bezierPoints.push({x: cubic(cx, t), y: cubic(cy, t)});
        }
    }
    return bezierPoints;
}

function subtract(a, b) {
    return {x: a.x - b.x, y: a.y - b.y};
}
function add(a, b) {
    return {x: a.x + b.x, y: a.y + b.y};
}

const demoPts = [
    {
        b: {x: 10, y: 60, lock:1},
        c: {x: 66, y: 74}
    },
    {
        a: {x: 348, y: 142},
        b: {x: 443, y: 138},
        c: {x: 538, y: 134},
        lock: 1,
    },
    {
        a: {x: 589, y: 82},
        b: {x: 600, y: 60, lock:1}
    },
], demoPts2 = [
    {
        b: {x: 10, y: 40, lock:1},
        c: {x: 30, y: 29}
    },
    {
        a: {x: 105, y: 12},
        b: {x: 158, y: 6},
        c: {x: 211, y: 0},
        lock: 1,
    },
    {
        a: {x: 545, y: 12},
        b: {x: 600, y: 40, lock:1}
    },
];

const App = createReactClass({
    getInitialState(){
        return {
            b1: {
                controls: demoPts,
                points: generateBezier(demoPts),
            },
            b2: {
                controls: demoPts2,
                points: generateBezier(demoPts2),
            },
        };
    },

    renderPathC(ctx, points){
        ctx.beginPath();
        ctx.strokeStyle = 'white';
        for (let i = 0; i < points.length; i++) {
            ctx.lineTo(points[i].x + 5, points[i].y + 5);
        }
        ctx.stroke();
        ctx.closePath();
    },

    renderLineC(ctx, a, b){
        ctx.beginPath();
        ctx.moveTo(a.x + 5, a.y + 5);
        ctx.lineTo(b.x + 5, b.y + 5);
        ctx.stroke();
    },

    renderControlC(ctx, control){
        ctx.strokeStyle = control.lock ? 'red' : 'white';
        if (control.a) this.renderLineC(ctx, control.a, control.b);
        if (control.c) this.renderLineC(ctx, control.b, control.c);
    },

    renderBezierC(ctx, bezier){
        this.renderPathC(ctx, bezier.points);
        bezier.controls.forEach(control => this.renderControlC(ctx, control));
    },

    renderFillC(ctx, points1, points2){
        ctx.beginPath();
        ctx.strokeStyle = 'beige';
        for (let i = 0; i < points1.length; i++) {
            ctx.lineTo(points1[i].x + 5, points1[i].y + 5);
        }
        for (let i = 0; i < points1.length; i++) {
            ctx.lineTo(points1[i].x + 5, points1[i].y + 5);
        }
        ctx.fill();
        ctx.closePath();
    },

    renderC() {
        const ctx = this.refs.canvas.getContext('2d');
        ctx.clearRect(0, 0, width, height);
        this.renderFillC(ctx,this.state.b1, this.state.b2);
        this.renderBezierC(ctx, this.state.b1);
        this.renderBezierC(ctx, this.state.b2);
    },

    renderHandle(point, onDrag, key, lock){
        const handleClass = `handle-${key}`;
        function handleDrag(e, data) {
            if (point.lock)return;
            onDrag({x: data.x, y: data.y}, {x: data.deltaX, y: data.deltaY});
        }

        return <Draggable
            axis="both"
            handle={'.' + handleClass}
            position={point}
            onDrag={handleDrag}
            key={key}
        >
            <div style={{
                ...styles.handle,
                background: 'white',
            }} className={handleClass}/>
        </Draggable>
    },

    renderControl(control, onChange, key){
        const children = [];

        const f = (...args) => {
            children.push(this.renderHandle(...args));
        };

        if (control.lock) {
            f(control.a, point => onChange({
                lock: true,
                a: point,
                b: control.b,
                c: add(control.b, subtract(control.b, point)),
            }), `${key}-a`, 1);
            f(control.c, point => onChange({
                lock: true,
                a: add(control.b, subtract(control.b, point)),
                b: control.b,
                c: point,
            }), `${key}-c`, 1);
        } else {
            if (control.a)
                f(control.a, point => onChange({...control, a: point}), `${key}-a`, 0);
            if (control.c)
                f(control.c, point => onChange({...control, c: point}), `${key}-c`, 0);
        }

        f(control.b, (point, delta) => onChange({
            lock: control.lock,
            a: control.a && add(control.a, delta),
            b: point,
            c: control.c && add(control.c, delta),
        }), `${key}-b`, control.lock);

        return children;
    },

    renderControls(controls, onChange){
        const copy = controls.slice();
        const handles = [];
        controls.forEach((control, index) => {
            handles.push(this.renderControl(control, newControl => {
                copy[index] = newControl;
                onChange(copy)
            },index));
        });
        return <div style={styles.controlPoints}>{handles}</div>
    },

    handleControlsChange(bezierId, newControls){
        const newState = {...this.state};
        newState[bezierId] = {
            controls: newControls,
            points: generateBezier(newControls),
        };
        this.setState(newState);
        this.renderC();
    },

    handleControlsChangeB1(...args){
        this.handleControlsChange('b1', ...args)
    },
    handleControlsChangeB2(...args){
        this.handleControlsChange('b2', ...args)
    },

    componentDidMount(){
        this.renderC();
    },

    render() {
        let res = "csv output:\n";
        const text = this.state.b1.controls.concat(this.state.b2.controls).forEach(control => {
            if (control.a)
                res += `${control.a.x},${control.a.y}\n`;
            res += `${control.b.x},${control.b.y}\n`;
            if (control.c)
                res += `${control.c.x},${control.c.y}\n`;
            return res;
        });
        return (
            <div>
                <div style={styles.parent}>
                    {this.renderControls(this.state.b1.controls, this.handleControlsChangeB1)}
                    {this.renderControls(this.state.b2.controls, this.handleControlsChangeB2)}
                    <canvas style={styles.canvas} ref="canvas" width={width} height={height}/>
                </div>
                <pre>{res}</pre>
            </div>
        );
    },
});

export default App;



